	module.exports = {"data":{"post":{"html":"<p>Our house turns 20 years old this year. And every spring the same thing happens. The ants come into the kitchen for a few weeks. Then they disappear.  </p>\n<p>Ours is a good, solid German house, made of brick. But we live out in the country, and out in the country one has to deal with critters and creepy-crawly things. The doors and windows are tight, but there is obviously a crack in the outside wall to the kitchen. Every spring the ants wake up, get hungry, come inside the wall, and then maybe behind the cabinets, and find an entry to the kitchen.</p>\n<p>Every year they find a <em>new</em> entry. I've heard that ants have collective intelligence, and that it's probably passed down from generation to generation. They are probably more intelligent than we, since every year we forget that the ants are coming. And then it takes us several days to find their point of entry.</p>\n<p>Since we can't think like ants, we have to wait until a whole bunch of them have come in so we can see their trail. Then we get out the ant spray, secure the entry, and wait a few days see if they find a new entry and come back.</p>\n<p>They usually don't. The spring weather gets warmer, and I presume the ants then find it easier to find their food outside rather than risk encountering ant spray inside. But they have the knowledge of how to get into our kitchen, and their offspring will use that knowledge next year, to find a new entry.</p>\n<p>Here is this year's point of entry, and our weapon of choice. We're planning on renovating our kitchen this year. Maybe we'll find that crack in the wall and get it patched.</p>\n<p><img src=\"/assets/ants-180416.jpg\" alt=\"The Ants\"></p>","fields":{"title":"The Ants","date":"18 Apr 2016","url":"https://papascott.de/2016-04-18-the-ants/"}}},"pathContext":{"slug":"/2016-04-18-the-ants/","prev":{"fields":{"title":"Student Exchange","date":"05 Apr 2016","slug":"/2016-04-05-student-exchange/"}},"next":{"fields":{"title":"Prince","date":"23 Apr 2016","slug":"/2016-04-23-prince/"}}}}

